.Entete{
    width: 100%;
    background-color: #333333;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    font-size: 1.3em;
    .logo{
        margin-left: 30px;
    }

    .avatar{
        margin-right: 30px;
        display: flex;
        align-items: center;

        img{
            margin-left: 30px;
            border-radius: 50px;
        }
    }
}